function menu_sorter(selected_id) {

	//Remove Click for the time being
	$('.main-navigation li a').removeAttr('onclick');

	// Reset to base when clicking home
	if (selected_id == 'home') {
		var toShow = $('.prod-cleansers, .prod-acne, .prod-eczema, .prod-baby');
		var backgrounder = $('.the-background2');
		var textHide = $('.bullet-text-cleanser, .bullet-text-acne, .bullet-text-eczema, .bullet-text-baby, .headline-cleanser, .headline-acne, .headline-eczema, .headline-baby');
		var textShow = $('.bullet-text, .headline');

		$(toShow).css('height','120px');		
		toShow.find('div img').css('height','120px');
		toShow.find('div').css('margin','0 1px');
		toShow.show('slow');
		$(backgrounder).css('display','none');
		toShow.promise().done(function() {
			$(textShow).css('display','block');			
			$(textHide).css('display','none');
			$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
		});
	}

	// Show only cleansers
	if (selected_id == 'cleansers') {
		var toShow = $('.prod-cleansers');
		var backgrounder = $('.the-background2');
		var toHide = $('.prod-acne, .prod-eczema, .prod-baby');
		var textHide = $('.bullet-text, .bullet-text-acne, .bullet-text-eczema, .bullet-text-baby, .headline, .headline-acne, .headline-eczema, .headline-baby');
		var textShow = $('.bullet-text-cleanser, .headline-cleanser');

		$(toShow).css('height','160px');		
		toShow.show('fade');
		$(backgrounder).css('display','none');
		toShow.find('div').animate({ 'marginLeft': '10px'},400);
		toShow.find('div img').animate({ 'height': '160px'},400);
			$(toHide).css('height','120px');
			toHide.hide('fade');

		toShow.promise().done(function(){

			toHide.promise().done(function() {
				toHide.find('div').css('margin','0 1px');
				$(textShow).css('display','block');			
				$(textHide).css('display','none');
				$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
			});
		});
	}

	// Show only acne
	if (selected_id == 'acne') {
		var toShow = $('.prod-acne');
		var backgrounder = $('.the-background2');
		var toHide = $('.prod-cleansers, .prod-eczema, .prod-baby');
		var textHide = $('.bullet-text-cleanser, .bullet-text, .bullet-text-eczema, .bullet-text-baby, .headline, .headline-cleanser, .headline-eczema, .headline-baby');
		var textShow = $('.bullet-text-acne, .headline-acne');


		$(toShow).css('height','160px');		
		toShow.show('fade');
		$(backgrounder).css('display','none');
		toShow.find('div').animate({ 'marginLeft': '10px'},400);
		toShow.find('div img').animate({ 'height': '160px'},400);
				
		toShow.promise().done(function(){
			$(toHide).css('height','120px');
			toHide.hide('fade');
			toHide.promise().done(function() {
				toHide.find('div').css('margin','0 1px');
				$(textShow).css('display','block');			
				$(textHide).css('display','none');
				$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
			});
		});
	}

	// Show only eczema
	if (selected_id == 'eczema') {
		var toShow = $('.prod-eczema');
		var backgrounder = $('.the-background2');
		var toHide = $('.prod-cleansers, .prod-acne, .prod-baby');
		var textHide = $('.bullet-text-cleanser, .bullet-text-acne, .bullet-text-baby, .bullet-text, .headline, .headline-acne, .headline-cleanser, .headline-baby');
		var textShow = $('.bullet-text-eczema, .headline-eczema');


		$(toShow).css('height','160px');		
		toShow.show('fade');
		$(backgrounder).css('display','none');
		toShow.find('div').animate({ 'marginLeft': '10px'},400);
		toShow.find('div img').animate({ 'height': '160px'},400);
				
		toShow.promise().done(function(){
			$(toHide).css('height','120px');
			toHide.hide('fade');
			toHide.promise().done(function() {
				toHide.find('div').css('margin','0 1px');				
				$(textShow).css('display','block');			
				$(textHide).css('display','none');
				$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
			});
		});
	}

		// Show only baby
	if (selected_id == 'baby') {
		var toShow = $('.prod-baby');
		var backgrounder = $('.the-background2');
		var toHide = $('.prod-cleansers, .prod-acne, .prod-eczema');
		var textHide = $('.bullet-text-cleanser, .bullet-text-acne, .bullet-text-eczema, .bullet-text, .headline, .headline-acne, .headline-cleanser, .headline-eczema');
		var textShow = $('.bullet-text-baby, .headline-baby');


		$(toShow).css('height','160px');		
		toShow.show('fade');
		backgrounder.show('fade');
		toShow.find('div').animate({ 'marginLeft': '10px'},400);
		toShow.find('div img').animate({ 'height': '160px'},400);
			$(toHide).css('height','120px');
			toHide.hide('fade');
				
		toShow.promise().done(function(){
			toHide.promise().done(function() {
				toHide.find('div').css('margin','0 1px');				
				$(textShow).css('display','block');			
				$(textHide).css('display','none');
				$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
			});
		});
	}
}